.highlightable {
  position: relative;
}

.highlightable::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  background-color: rgba(0, 76, 153, .5);
  border: 1px solid rgb(0, 127, 255);
  opacity: 0;
}

.highlight::after {
  -webkit-animation: highlight 1.5s ease-out;
          animation: highlight 1.5s ease-out;
}

@-webkit-keyframes highlight {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes highlight {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

